/* src/App.css - Täysin responsiivinen versio */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background: #000;
  color: #fff;
  overflow-x: hidden;
  min-height: 100vh;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 80px);
}

.page {
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.btn {
  background: linear-gradient(135deg, #333, #555);
  color: #fff;
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  white-space: nowrap;
}

.btn:hover {
  background: linear-gradient(135deg, #555, #777);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255,255,255,0.1);
}

/* Ostoskori ikoni */
.cart-icon {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1001;
  background: linear-gradient(135deg, #333, #555);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.3);
}

.cart-icon:hover {
  background: linear-gradient(135deg, #555, #777);
  transform: scale(1.1);
}

.cart-count {
  position: absolute;
  top: -8px;
  right: -8px;
  background: #ff4444;
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: bold;
}

/* Ostoskori popup */
.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.8);
  z-index: 1002;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.cart-popup {
  background: linear-gradient(135deg, #111, #222);
  border-radius: 15px;
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  max-height: 80vh;
  overflow-y: auto;
  border: 1px solid #444;
  position: relative;
}

.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #333;
  padding-bottom: 1rem;
}

.cart-header h2 {
  color: #fff;
  margin: 0;
  font-size: 1.5rem;
}

.cart-close {
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}

.cart-close:hover {
  background: #333;
}

.cart-empty {
  text-align: center;
  color: #ccc;
  padding: 2rem;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #333;
  gap: 1rem;
}

.cart-item:last-child {
  border-bottom: none;
}

.item-info h4 {
  margin-bottom: 0.25rem;
  color: #fff;
  font-size: 1rem;
}

.item-info p {
  color: #ccc;
  font-size: 0.9rem;
  margin: 0;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
}

.quantity-btn {
  background: linear-gradient(135deg, #333, #555);
  border: none;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.quantity-btn:hover {
  background: linear-gradient(135deg, #555, #777);
}

.quantity-display {
  background: #333;
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 5px;
  min-width: 40px;
  text-align: center;
  font-size: 0.9rem;
}

.remove-btn {
  background: #ff4444;
  border: none;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: background 0.3s ease;
  white-space: nowrap;
}

.remove-btn:hover {
  background: #ff6666;
}

.cart-total {
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 2px solid #444;
  text-align: center;
}

.total-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 1rem;
}

.checkout-btn {
  background: linear-gradient(135deg, #4CAF50, #45a049);
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 100%;
}

.checkout-btn:hover {
  background: linear-gradient(135deg, #45a049, #3d8b40);
  transform: translateY(-2px);
}

/* Hero section */
.hero {
  text-align: center;
  padding: 4rem 0;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #fff, #aaa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.2;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #ccc;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Product grids */
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.product-card {
  background: linear-gradient(135deg, #111, #222);
  border-radius: 10px;
  padding: 1.5rem;
  border: 1px solid #333;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(255,255,255,0.1);
}

.product-card h3 {
  margin-bottom: 1rem;
  color: #fff;
  font-size: 1.1rem;
  line-height: 1.3;
}

.product-card p {
  color: #ccc;
  margin-bottom: 1rem;
  flex: 1;
  font-size: 0.95rem;
  line-height: 1.4;
}

.price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 1rem;
  text-align: center;
}

/* Info & Contact styles */
.info-list {
  margin: 1rem 0;
  padding-left: 2rem;
  color: #ccc;
  line-height: 1.6;
}

.partners-text {
  color: #ccc;
  line-height: 1.6;
}

.info-section h3 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: #fff;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #fff;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  background: #222;
  border: 1px solid #444;
  border-radius: 5px;
  color: #fff;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #666;
  background: #333;
  box-shadow: 0 0 0 2px rgba(76, 175, 80, 0.2);
}

.contact-info {
  margin-top: 3rem;
  text-align: center;
}

.contact-info h3 {
  margin-bottom: 1rem;
  color: #fff;
}

.contact-info p {
  color: #ccc;
  margin: 1rem 0;
  line-height: 1.6;
}

/* Tablet styles (768px - 1024px) */
@media (max-width: 1024px) {
  .container {
    padding: 1.5rem;
  }
  
  .product-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
  }
  
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .cart-icon {
    width: 55px;
    height: 55px;
    font-size: 1.3rem;
  }
  
  .cart-count {
    width: 22px;
    height: 22px;
    font-size: 0.75rem;
  }
}

/* Mobile styles (max 768px) */
@media (max-width: 768px) {
  .container {
    padding: 1rem;
    min-height: calc(100vh - 120px); /* Lisää tilaa navigaatiolle */
  }
  
  .hero {
    padding: 2rem 0;
  }
  
  .hero h1 {
    font-size: 2rem;
    line-height: 1.1;
  }
  
  .hero p {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .btn {
    padding: 0.6rem 1.5rem;
    font-size: 0.9rem;
    width: 100%;
    max-width: 300px;
  }
  
  .product-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    margin-top: 1.5rem;
  }
  
  .product-card {
    padding: 1.25rem;
  }
  
  .product-card h3 {
    font-size: 1rem;
    margin-bottom: 0.75rem;
  }
  
  .product-card p {
    font-size: 0.9rem;
    margin-bottom: 0.75rem;
  }
  
  .price {
    font-size: 1.3rem;
    margin-bottom: 0.75rem;
  }
  
  /* KORJATTU: Ostoskori mobile - siirretty pois burger-menun tieltä */
  .cart-icon {
    top: 85px; /* Navigaation alapuolelle */
    right: 15px;
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
    z-index: 999; /* Vähemmän kuin navigaatio (1000) */
  }
  
  .cart-count {
    width: 20px;
    height: 20px;
    font-size: 0.7rem;
    top: -6px;
    right: -6px;
  }
  
  .cart-popup {
    padding: 1.5rem;
    margin: 0.5rem;
    max-height: 85vh;
  }
  
  .cart-header h2 {
    font-size: 1.3rem;
  }
  
  .cart-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
    padding: 1rem 0;
  }
  
  .item-info {
    width: 100%;
  }
  
  .item-info h4 {
    font-size: 0.95rem;
    margin-bottom: 0.5rem;
  }
  
  .item-info p {
    font-size: 0.85rem;
  }
  
  .quantity-controls {
    align-self: flex-end;
    gap: 0.75rem;
  }
  
  .quantity-btn {
    width: 35px;
    height: 35px;
    font-size: 1.1rem;
  }
  
  .quantity-display {
    padding: 0.4rem 0.8rem;
    min-width: 45px;
  }
  
  .remove-btn {
    padding: 0.4rem 0.75rem;
    font-size: 0.8rem;
  }
  
  .total-price {
    font-size: 1.3rem;
  }
  
  .checkout-btn {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
  }
  
  /* Form styles mobile */
  .contact-form {
    padding: 0;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.8rem;
    font-size: 1rem; /* Estää zoom iOS:ssä */
  }
  
  .form-group label {
    font-size: 0.95rem;
  }
  
  /* Info sections mobile */
  .info-list {
    padding-left: 1.5rem;
    font-size: 0.95rem;
  }
  
  .contact-info p {
    font-size: 0.95rem;
  }
}

/* Small mobile styles (max 480px) */
@media (max-width: 480px) {
  .container {
    padding: 0.75rem;
    min-height: calc(100vh - 130px); /* Lisää tilaa pienille näytöille */
  }
  
  .hero {
    padding: 1.5rem 0;
  }
  
  .hero h1 {
    font-size: 1.75rem;
  }
  
  .hero p {
    font-size: 0.95rem;
  }
  
  .product-card {
    padding: 1rem;
  }
  
  /* Ostoskori pienille näytöille */
  .cart-icon {
    top: 90px; /* Vielä hieman alemmaksi pienillä näytöillä */
    right: 10px;
    width: 45px;
    height: 45px;
    font-size: 1.1rem;
  }
  
  .cart-count {
    width: 18px;
    height: 18px;
    font-size: 0.65rem;
    top: -5px;
    right: -5px;
  }
  
  .cart-popup {
    padding: 1rem;
    margin: 0.25rem;
    border-radius: 10px;
  }
  
  .cart-header {
    margin-bottom: 1rem;
    padding-bottom: 0.75rem;
  }
  
  .cart-header h2 {
    font-size: 1.2rem;
  }
  
  .cart-close {
    width: 30px;
    height: 30px;
    font-size: 1.2rem;
  }
  
  .quantity-controls {
    gap: 0.5rem;
  }
  
  .quantity-btn {
    width: 32px;
    height: 32px;
  }
  
  .quantity-display {
    padding: 0.3rem 0.6rem;
    min-width: 40px;
    font-size: 0.85rem;
  }
  
  .remove-btn {
    padding: 0.3rem 0.6rem;
    font-size: 0.75rem;
  }
}

/* Landscape orientation adjustments */
@media (max-height: 500px) and (orientation: landscape) {
  .hero {
    padding: 1rem 0;
  }
  
  .hero h1 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }
  
  .hero p {
    margin-bottom: 1rem;
  }
  
  .cart-icon {
    top: 70px; /* Landscape-tilassa korkeampi sijainti */
  }
  
  .cart-popup {
    max-height: 90vh;
    padding: 1rem;
  }
  
  .cart-header {
    margin-bottom: 0.75rem;
    padding-bottom: 0.5rem;
  }
}

/* High DPI screens */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .btn {
    border: none;
  }
  
  .cart-icon {
    border: 1px solid rgba(255,255,255,0.1);
  }
  
  .product-card {
    border: 1px solid #333;
  }
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .page {
    animation: none;
  }
  
  .cart-icon:hover {
    transform: none;
  }
  
  .product-card:hover {
    transform: none;
  }
  
  .btn:hover {
    transform: none;
  }
}

/* Dark mode support (if user prefers dark) */
@media (prefers-color-scheme: dark) {
  /* Already dark by default, but ensure consistency */
  body {
    background: #000;
    color: #fff;
  }
}

/* Print styles */
@media print {
  .cart-icon,
  .cart-popup,
  .cart-overlay {
    display: none !important;
  }
  
  .navigation {
    position: static !important;
  }
  
  .hero {
    page-break-inside: avoid;
  }
  
  .product-card {
    page-break-inside: avoid;
    margin-bottom: 1rem;
  }
}